/* eslint-disable react-hooks/exhaustive-deps */
import React, { useContext, useEffect, useState } from "react";
import {
    NavLink,
    Link
} from "react-router-dom";
import { useTranslation } from "react-i18next"


import "../../styles/Nav.scss";
import logo from "../../images/logo.png";

import { FcManager } from "react-icons/fc";
import { FaHome, FaUserEdit } from "react-icons/fa"
import { InitContext } from "../../contexts/initContext";
import { DataContext } from "../../contexts/dataContext";

function Navbar (props) {
    const { transactionUsersUrl } = useContext(InitContext);
    const { t, i18n } =useTranslation()
    const { 
        thisUser, setThisUser,
        runFetch, setRunFetch
    } = useContext(DataContext)
    const [isUserCard, setIsUserCard] = useState(false)

    useEffect(()=>{
        console.log(localStorage.getItem("userid"))
        localStorage.getItem("userid")===null && props.history.goBack()
   
        fetch(transactionUsersUrl, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                action: "Query",
                userid: localStorage.getItem("userid")
            })
        })
        .then(res=>res.json())
        .then(data=>{
            console.log(data)
            setThisUser(data[0])
        })
        .catch(err=>console.error(err))

    }, [runFetch])

    const clickUserIcon = (e) => {
        setIsUserCard(!isUserCard)
    }

    const clickUserCard = (e) => {
        setIsUserCard(false)
    }

    return(
        <header className="app-header">
            {/* {props.location.state===undefined && props.history.push("/404")} */}
            <nav className="app-nav">
                <a href="/"><img src={logo} alt="logo" /></a>
                <div className="menu">
                    <div className="user-icon" onClick={clickUserIcon}><FcManager/>
                        <div className="user-card" onClick={clickUserCard} style={{display: isUserCard ? "block" : "none" }}>
                            <div className="top-div">
                                <FcManager/>
                                <p>{thisUser.userid}</p>
                                <p>{thisUser.email}</p>
                            </div>
                            <div className="bottom-div">
                                <Link to={{
                                    pathname: `${props.match.path}/myaccount`,
                                    // state: props.location.state
                                    state: props.thisUser
                                    }}>{t("my_account")}</Link>
                                <a href="/" onClick={()=> localStorage.removeItem("userid")} >{t("logout")}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>
            <nav className="control-nav">
                <NavLink exact 
                    to={{
                        pathname: `/app`,
                        // state: props.location.state
                        state: props.thisUser
                    }}
                    activeClassName="control-nav-active"
                >
                    <FaHome/><p>{t("home")}</p>
                </NavLink>
                <NavLink exact 
                    to={{
                        pathname: `/app/myaccount`,
                        // state: props.location.state
                        state: props.thisUser
                    }}
                    activeClassName="control-nav-active" 
                >
                    <FaUserEdit/><p>{t("my_account")}</p>
                </NavLink>
            </nav>
        </header>

    )
}

export default Navbar;