import { useContext, useState } from "react";
import { DataContext } from "../../contexts/dataContext";

import "../../styles/UpdateBox.scss";

const UpdateBox = (props) => {
    const { runFetch, setRunFetch } =useContext(DataContext);
    const [updateUser, setUpdateUser] = useState(props.thisUser);

    const changeInput = e => {
        // setThisUser({...thisUser, [e.target.name]: e.target.value})
        setUpdateUser({...updateUser, [e.target.name]: e.target.value})
    }

    const onSubmit = e => {
        e.preventDefault();

        console.log({...updateUser, action: "Update"})

        fetch(props.transactionUsersUrl, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({...updateUser, action: "Update"})
        })
        .then(res=>res.json())
        .then(data=>{
            console.log(data)
            if (data.result.toLowerCase().indexOf("ok")!==-1 || data.result.toLowerCase().indexOf("success")!==-1) {
                alert(data.result)
                props.setAlert(data.result)
                props.setIsUpdateBtn(false);
                setRunFetch(!runFetch)
            }
        })
        .catch(err=>console.error(err))

    }

    return(
        <div className="overlay" id="UpdateBox">
            <form  onSubmit={onSubmit}>
                <div className="x-btn" onClick={()=>props.setIsUpdateBtn(false)}>X</div>
                <div className="personal-info-inputs">
                    <div className="input-div">
                        <label>
                            <p>user id<span>*</span></p>
                            <input name="userid" type="text"  value={updateUser.userid===null ? "" : updateUser.userid} onChange={changeInput} disabled />
                        </label>
                        <label>
                            <p>password<span>*</span></p>
                            <input name="password" type="password"  value={updateUser.password===null ? "" : updateUser.password} onChange={changeInput} required />
                        </label>
                        <label>
                            <p>company</p>
                            <input name="company" type="text"  value={updateUser.company===null ? "" : updateUser.company} onChange={changeInput} />
                        </label>
                        <label>
                            <p>user name</p>
                            <input name="username" type="text"  value={updateUser.username===null ? "" : updateUser.username} onChange={changeInput} />
                        </label>
                        <label>
                            <p>title</p>
                            <input name="title" type="text"  value={updateUser.title===null ? "" : updateUser.title} onChange={changeInput} />
                        </label>
                        <label>
                            <p>email<span>*</span></p>
                            <input name="email" type="email"  value={updateUser.email===null ? "" : updateUser.email} onChange={changeInput} required />
                        </label>
                        <label>
                            <p>birthday</p>
                            <input name="birthday" type="date"  value={updateUser.birthday===null ? "" : updateUser.birthday} onChange={changeInput} />
                        </label>
                        <label>
                            <p>gender</p>
                            <select name="gender"  value={updateUser.gender===null ? "" : updateUser.gender} onChange={changeInput} >
                                <option value=""></option>
                                <option value="m">male</option>
                                <option value="f">female</option>
                            </select>                        
                        </label>
                        <label>
                            <p>telephone</p>
                            <input name="telephone" type="tel"  value={updateUser.telephone===null ? "" : updateUser.telephone} onChange={changeInput} pattern="^[0-9-+\s()]*$" />
                        </label>
                        <label>
                            <p>mobile</p>
                            <input name="mobile" type="tel"  value={updateUser.mobile===null ? "" : updateUser.mobile} onChange={changeInput} pattern="^[0-9-+\s()]*$" /> 
                        </label>
                    </div>
                        <div className="address-div">
                            <label><p>zipcode</p>
                                <input name="zipcode" type="text" value={updateUser.zipcode===null ? "" : updateUser.zipcode} onChange={changeInput} />
                            </label>
                            <label><p>address</p>
                                <input name="address" type="text" value={updateUser.address===null ? "" : updateUser.address} onChange={changeInput} /> 
                            </label>              
                        </div>    
                    </div>
                    <input type="submit" value="update"/>
                </form>            
        </div>

    )
}

export default UpdateBox;